<template>
  <!-- <MuYu/> --> 
    <Header v-if="HeadeShow"></Header>
    <keep-alive :include="['HomeIndex', 'LiFoTang', 'ShangDian', 'DuoBaoGe', 'XuyuanShu', 'WoDe']">
      <component :is="active" :class="[HeadeShow ? 'customPadding' : '']"></component>
    </keep-alive>
    <ShangDian v-show="active == 'LiFoTang'"></ShangDian>
    <TarBar @activeHandel="activeHandel" safe-area-inset-bottom></TarBar> 
</template>

<script>
import LiFoTang from '@/components/lifotang/Muyu.vue';
import HomeIndex from '@/components/home/';
import ShangDian from "@/components/shangdian/";
import DuoBaoGe from "@/components/duobaoge/";
import WoDe from "@/components/wode";
import XuyuanShu from "@/components/xuyuanshu"; //许愿树
import Header from "@/components/header"; //头部
import TarBar from "@/components/tabBar"; //底部导航
export default {
  name: 'AppIndex',
  components: {
    HomeIndex, LiFoTang, ShangDian, DuoBaoGe, WoDe, XuyuanShu, TarBar, Header
  },
  data() {
    return {
      active: 'HomeIndex'
    }
  },
  computed: {
    HeadeShow() {
      return ['HomeIndex'].includes(this.active)
    }
  },
  methods: {
    activeHandel(val) {
      console.log(val)
      this.active = val;
    },

  }
}
</script>
<style lang="less" scoped>
.customPadding {
  padding-top: 78px;
}
</style>
<style>
* {
  margin: 0;
  padding: 0;
}
</style>