const state = {
    userInfo:{
        name:'糖糖',
        gender:1,//1男性 2女性
    },
    asset:{
        ingot:999999999,//元宝
    },
    gdSum:localStorage.getItem('gdSum')?localStorage.getItem('gdSum'):0,
    buyArticle:localStorage.getItem('buyArticle')?JSON.parse(localStorage.getItem('buyArticle')):[],
    xiangzhu:{
        num:2,
        startTime:'',
        endTime:''
    }
}
const mutations = {
    setGdSum(state,gdSum){
        state.gdSum=gdSum;
        localStorage.setItem('gdSum',gdSum)
    },
    setUserInfo(state,info){
        state.userInfo=info;
    },
    setIngot(state,ingot){
        state.asset.ingot=ingot;
    }
}
const actions = {

}
export default {state,mutations,actions,namespaced:true}