<template>
  <div class="shopWrap">
    <Search></Search>
    <div class="shopBox">
      <div v-for="(item, index) in dataList" :key="index" class="shopItme" @click="gotoDetail">
        <img :src="item.image" alt="" class="imgTag">
        <div style="padding:10px;">
          <p class="tag-label">{{ item.shortLabel ?? item.label }}</p>
          <p class="tag-box"><span class="tag-icon">¥</span><span class="tag-price">{{ priceFormat(item) }}</span>
            <span class="tag-xs" v-if="priceFormat(item, false) > 0">.{{ priceFormat(item, false) }}</span>
            <span class="tag-xs">{{ item.unit }}</span>
            <span class="tag-ms" v-if="sellNum == 0">{{ item.sellNum }}人加购</span>
            <span class="tag-ms" v-else>{{ sellFormat(item.sellNum) }}人付款</span>
          </p>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import sp1 from './images/sp1.webp';
import sp2 from './images/sp2.webp';
import sp3 from './images/sp3.webp';
import sp4 from './images/sp4.webp';
import Search from '@/components/search'
export default {
  name: 'DuoBaoGe',
  components: {
    Search
  },
  mixins: [],
  props: {

  },
  data() {
    return { 
      dataList: [
        {
          image: sp1,
          label: '【普陀山祈愿】黄铜十二生肖本命佛五帝钱朱砂葫芦护身保平安挂件',
          price: '18.68',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },
        {
          image: sp2,
          label: '2024龙年化解黄龙玉属鸡十二生肖鸡吊坠转运男女款鸡挂件项链礼物',
          price: '9.9',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },
        {
          image: sp3,
          label: '大肚弥勒佛吊坠玉佛女男士款貔貅天然玉翡翠A货项链玉佩大号玉坠',
          price: '39.9',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },
        {
          image: sp4,
          label: '天然翡翠A货山水玉牌招财护身男女士款中号玉石项链玉佩挂件玉坠',
          price: '98',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        }, {
          image: sp4,
          label: '天然翡翠A货山水玉牌招财护身男女士款中号玉石项链玉佩挂件玉坠',
          price: '98',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        }, {
          image: sp1,
          label: '【普陀山祈愿】黄铜十二生肖本命佛五帝钱朱砂葫芦护身保平安挂件',
          price: '18.68',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },
        {
          image: sp2,
          label: '2024龙年化解黄龙玉属鸡十二生肖鸡吊坠转运男女款鸡挂件项链礼物',
          price: '9.9',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },
        {
          image: sp3,
          label: '大肚弥勒佛吊坠玉佛女男士款貔貅天然玉翡翠A货项链玉佩大号玉坠',
          shortLabel: '大肚弥勒佛吊坠',
          price: '39.9',
          isQ: false,//是否有券
          q: 0,//券的数值
          sellNum: 10000,//卖出数量
          unit: '',
        },

      ]
    }
  },
  computed: {

  },
  watch: {

  },
  mounted() {

  },
  methods: {
    priceFormat(item, isParseInt = true) {

      // 价钱格式化
      if (item.price / 100000000 > 1) {
        item.unit = '亿';
        if (isParseInt) { return parseInt(item.price / 100000000) }
        else { return String(item.price / 100000000).substr(this.substrFormat(item.price / 100000000)) }
      } else if (item.price / 10000 > 1) {
        item.unit = '万';
        if (isParseInt) { return parseInt(item.price / 10000) }
        else { return String(item.price / 10000).substr(this.substrFormat(item.price / 10000)) }
      } else {
        if (isParseInt) { return parseInt(item.price / 1) }
        else { return String(item.price / 1).substr(this.substrFormat(item.price / 1)) }
      }
    },
    substrFormat(val) {
      val = String(val);
      if (val.lastIndexOf('.') > -1) {
        return val.lastIndexOf('.') + 1
      } else {
        return -1;
      }
    },
    sellFormat(sellNum) {
      // 卖出数量格式化
      if (sellNum / 100 < 0) {
        return sellNum
      } else if (sellNum / 10000 >= 1) {
        if (sellNum % 10000 == 0) {
          return (sellNum / 10000) + '万';
        } else {
          return (sellNum / 10000).toFixed(2) + '万 +';
        }
      } else if (sellNum / 100 > 1) {
        return parseInt(sellNum / 100) * 100 + '+';
      }
    },
    // 跳转到详情页
    gotoDetail(item) {
      this.$router.push({
        path: 'details',
        query: {
          label: item.label
        }
      })
    },
  }
};
</script>
<style lang='less' scoped>
.shopWrap {
