    width: 100%;
    columns: 2;
    padding: 10px 10px 48px;
    padding-bottom: calc(48px + env(safe-area-inset-top) + env(safe-area-inset-bottom));
    box-sizing: border-box;
  }
 

  .shopItme {
    flex: 1;
    background: #fff;
    border-radius: 4px;
    // min-width:49%;
    height: auto;
    margin-bottom: 20px;
    break-inside: avoid;

    .imgTag {
      width: 100%;
    }

    .tag-label {
      font-size: 16px;
      color: #333;
      font-family: fangsong;
      white-space: nowrap;
      overflow: hidden;
      margin-bottom: 10px;
    }

    .tag-box {
      .tag-icon {
        color: red;
        font-size: 12px;
      }

      .tag-price {
        color: red;
        font-family: math;
        font-weight: 900;
        font-size: 16px;
      }

      .tag-xs {
        color: red;
        font-family: math;
        font-weight: 900;
        font-size: 13px;
      }

      .tag-ms {
        font-size: 13px;
        margin-left: 10px;
      }
    }
  }
}
</style>