<template>
   <div class="xuyuanshuWrap"> 
    <nav-title title="许愿树" background="transparent"></nav-title>
    <div class="xuyuanshuBox" :class="[scale?'scale':'']" @click="shuHandel"></div>
    <!-- <img src="./images/xuyuanshu.png" alt="" class="xysImg"> -->
    <van-number-keyboard  />
   </div>
</template>
<script>
import NavTitle from "@/components/nav-title"
export default {
   name: 'XuyuanShu',
   components: {
    NavTitle
   },
   mixins: [],
   props: {
     
   },
   data() {
     return {
        scale:false,
     }
   },
   computed: {
     
   },
   watch: {
     
   },
   mounted() {
     
   },
   methods: {
    shuHandel(){
        if(this.scale) return;
        this.scale=true;
        setTimeout(()=>{
            this.scale=false;
        },500)
    }
   }
};
</script>
<style lang='less' scoped>
 @keyframes  scale{
        0% {transform: scale(1)};
        25% {transform: scale(1.02)}
        50% {transform: scale(1)};
        75% {transform: scale(.98)};
        100% {transform: scale(1)}
    }
.xuyuanshuWrap{
    position: relative;
    background: linear-gradient(to bottom, #428ab5, #cadb5d);
    width:100%;
    // height:calc(100vh - 48px);
    height:100vh;
    display: flex;
    align-items: center;
    .xuyuanshuBox{
        background:url('./images/xuyuanshu.png') no-repeat center bottom ;
        width: 90%;
        height: 75%;
        background-size: contain;
        margin: auto;
        transform: scale(1);
        transform-origin: bottom;
    }
    .scale{
        animation:scale .5s linear;
    }
    .xysImg{
        width:100%;
    }
    
}
</style>
