<!-- 我的页面 -->
<template>
    <div class="myWrap" safe-area-inset-top>
        <div class="user-box">
            <div class="avatarBox">
                <img src="./avatar.svg" alt="">
            </div>
            <div class="userInfo">
                <h5>{{userInfo.name}}</h5>
                <div class="baseType"><img :src="userInfo.gender==1?man:woman" alt="" style="width: 14px;"><span>{{ userInfo.gender==1?'男':'女' }}</span></div>
                <div class="tag-box">
                    <span class="tag-vip">vip8</span>
                    <span class="tag-yb"><img src="@/assets/images/100002.png" />{{ ingot }} </span>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import man from "./images/sexMan.svg";
import woman from "./images/sexWoman.svg";
export default {
    name: 'WoDe',
    components: {

    },
    mixins: [],
    props: {

    },
    data() {
        return {
            userInfo:'',
            man,woman,
            ingot:this.$store.state.user.asset.ingot,//999999999
        }
    },
    computed: {

    },
    watch: {
        '$store.state.user.userInfo':{
            handler(newVal){
                this.userInfo=newVal;
            },
            deep:true,
            immediate:true
        }
    },
    mounted() {

    },
    methods: {

    }
};
</script>
<style lang='less' scoped>
.myWrap {
    width: 100%;
    height: 100%;
    padding: 20px 10px;
    box-sizing: border-box;
    .user-box{
        display:flex;
        .userInfo{
            margin-left: 8px;
            color:#fff;
            display: flex;
            flex-direction: column;
            // align-items: center;
            justify-content: center;
            .baseType{
                margin-top: 5px;
                display:flex;
                align-items:center;
                font-size:12px;
                color:#fff;
                img{
                    margin-right:6px;
                }
            }
            .tag-box{
                margin-top:2px;
                display: flex;
                align-items: center;
                font-size: 13px;
                grid-column-gap: 8px;
                .tag-vip{
                    background: rgba(225, 225, 225, .2);
                    padding:2px 8px;
                    border-radius: 2px;
                }
                .tag-yb{
                    background: rgba(225, 225, 225, .2);
                    padding:2px 8px;
                    border-radius: 2px;
                    img{
                        width: 14px;
                        margin-right:2px;
                    }
                }
            }
        }
        
    }
    .avatarBox {
        width: 64px;
        height:64px;
        display: flex;
        align-items: center;
        border-radius: 50%;
        box-shadow: 0 -2px 5px rgba(225, 225, 225, 1); 
