<template>
    <div class="tipWrap" v-show="show">
        <div class="tipBox">
            <p class="tip-p">{{content}}</p>
            <div class="btn-box">
                <span class="cancel-btn" @click="close">取 消</span>
                <span class="sure-btn" @click="confirm">确 定</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TipMessages',
    components: {

    },
    mixins: [],
    props: { 
        tipShow:{
            type:Boolean,
            default:false
        },//余额不足提示
        content:{
            type:String,
            default:"你的元宝不足，是否充值？"
        }
    },
    data() {
        return { 
            show:false,
        }
    },
    computed: { 
    },
    watch: {
        tipShow(val){
            this.show=val;
        },
    },
    mounted() {

    },
    methods: {
        close(){
            // this.show=false;
            this.$emit('close',false)
        },
        confirm(){
            
            this.$emit('confirm',true)
        },
    }
};
</script>
<style lang='less' scoped>
.tipWrap {
    
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    z-index: 1010;
    display: flex;
    align-items: center;
    justify-content: center;

    .tipBox {
        max-width: 300px;
        width: 76%;
        height: 100px;
        border: 1px solid #333;
        background: #ffd7ad;
        border-radius: 4px;
        box-shadow: 0 0 5px #0d0701 inset;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        grid-row-gap: 20px;

        .tip-p {
            font-size: 14px;
        }

        .btn-box {
            font-size: 12px;
            color: #fff;
            font-family: cursive;
            display: flex;
            justify-content: space-around;
            align-items: center;
            width: 100%;
            white-space: 10px;

            .cancel-btn {
                height: 15px;
                padding: 1px 16px;
                background: rgb(98 98 98);
                border-radius: 3px;
            }

            .sure-btn {
                height: 24px;
                padding: 0px 22px;
                background: radial-gradient(#284741, rgba(2, 133, 115, 0.5));
                border-radius: 3px;
                display: flex;
                align-items: center;

                &::hover {
                    background: radial-gradient(rgba(40, 71, 65, .8), rgba(2, 133, 115, 0.5));
                }
            }
        }
    }
}
</style>