<template>
  <div class="muyu">
    <Back v-if="false"></Back>
    <Marquee v-if="false"></Marquee>
    <p class="gdSum"><span>今天功德：</span><span>{{ gdSum }}</span></p>

    <div class="centenBox">
      <div class="tu-label" @click="descShow = !descShow">
        <span>{{ obj.label }}</span>
        <div class="tu-desc" v-show="descShow" @click.stop>
          <p>{{ obj.desc }} </p>
          <p style="text-align: right;">
            <span class="tu-xq" @click="goToMoreDetal">更多>></span>
          </p>
        </div>
      </div>
      <img :src="obj.img" :alt="obj.label" class="foxiang">
      <div class="gongzhuo-box">
        <div class="xiangzhuWrap">
          <img src="@/assets/images/ranshaoxiangzhu.png" alt="香烛左">
          <img src="@/assets/images/ranshaoxiangzhu.png" alt="香烛右">
        </div>
        <img src="./images/xianglu.png" alt="香炉" class="xianglu" style="">
        <img src="../../assets/images/gongdexiang/gongdexiang.png" alt="功德箱，日行一善" class="gongdexiang" @click="moneyMa">
      </div>

    </div>

    <div class="audioBox">
      <!-- <div :class="[isClick?'gdj1':'']" class="text-gd gdj1">功德+1</div> -->
      <div class="text-gd gdj1" v-for="item in gdData" :key="item">功德+1
        <audio :src="muyuAudio" autoplay style="display: none;"></audio>
      </div>
      <div @click="bodyClick" style="text-align: center;">
        <img src="@/assets/images/muyu/muyu.png" alt="" class="img">
        <!-- <img src="@/assets/images/muyu/muchui.png" alt="" class='muchuiImg' :class="[isClick?'muchuiImgClick':'']"> -->
      </div>

    </div>
    <div class="dialog-box" v-show="dialogShow">
      <img src="./images/wxEwm.png" alt="">
      <p @click="dialogShow = false" class="closeDialog">取消</p>
    </div>
    <Audio></Audio>
    <ShangDian></ShangDian>
    <BaoGuo></BaoGuo>
  </div>

</template>

<script>
import Back from '@/components/back-icon';
import muyuAudio from '@/assets/images/muyu/muyuAudio.mp3'
import Audio from "@/components/audio";
import Marquee from "@/components/marquee";
import ShangDian from "@/components/shangdian/";
import fo2 from "@/assets/images/shijiamoni/fo2.png";

import BaoGuo from "@/components/bao-guo";
export default {
  name: 'LiFoTang',
  components: {
    Audio, Marquee, Back, ShangDian,BaoGuo
  },
  props: {

  },
  data() {
    return {
      muyuAudio,
      isClick: false,
      gdSum: this.$store.state.user.gdSum,
      gdData: 0,
      time: 0,
      outTime: '',
      dialogShow: false,
      obj: {
        img: fo2,
        label: '佛'
      },
      descShow: false,//简介是否显示
    }
  },
  mounted() {
    console.log()
  },
  watch: {
    "$route": {
      handler(val) {
        // console.log(val)
        this.obj = val.query;
        this.descShow = false;
      },
      immediate: true,
    }
  },
  methods: {
    bodyClick() {
      clearTimeout(this.outTime)
      this.isClick = true;
      this.gdData++;
      this.gdSum++;
      this.$store.commit('user/setGdSum', this.gdSum)
      this.outTime = setTimeout(() => {
        this.gdData = 0;
      }, 1000)
    },
    moneyMa() {
      this.dialogShow = true;
    },
    goToMoreDetal(){
      this.$router.push({
        path:"/moreDeital",
        query:{
          label:this.obj.label
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.muyu {
  background: url(../../assets/images/TeamMatchProcessBg.png) no-repeat bottom;
  background-size: cover;
  perspective: 500px;
  position: relative;
  height: calc(100%);
  box-sizing: border-box;
  padding-top: calc(28px + env(safe-area-inset-top));
  padding-bottom: env(safe-area-inset-bottom);
}



.gdSum {
  font-size: 14px;
  color: #fff;
  position: absolute;
  right: 10px;
  top: 28px;
}



.img {
  pointer-events: none;
  width: 50%;
  max-width: 150px;
  cursor: url('../../assets/images/muyu/my.png'), pointer;
}


.centenBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;

  .tu-label {
    background: url('./images/lable-bg.png') no-repeat center;
    background-size: cover;
    width: 43px;
    height: 120px;
    font-size: 13px;
    color: #fff;
    writing-mode: vertical-lr;
    display: flex;
    justify-content: center;
    align-items: center;
    // transform: scale(.6);
    position: absolute;
    left: 40px;
    top: 40px;

    &::before {
      content: '?';
      display: block;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: red;
      position: absolute;
      top: -6px;
      right: -6px;
      writing-mode: lr-tb;
      font-size: 10px;
      text-align: center;
    }

    .tu-desc {
      width: 230px;
      padding: 10px;
      line-height: 1.5;
      background: rgba(0, 0, 0, 0.6);
      white-space: wrap;
      writing-mode: lr-tb;
      position: absolute;
      left: 48px;
      top: 10px;
      font-size: 12px;

      .tu-xq {
        color: red;
        text-align: right;
      }
    }
  }

  .foxiang {
    width: auto;
    max-width: 300px;
    max-height: 399px;
    margin-top: 30px;
  }

  .gongzhuo-box {
    position: relative;
    width: 100%;
    text-align: center;
    transform: translateY(-12%);

    .xiangzhuWrap {
      display: flex;
      justify-content: center;
      z-index: 10;
      grid-column-gap: 23.5%;
      width: 42%;
      max-width: 180px;
      position: absolute;
      top: -48%;
      left: 50%;
      transform: translateX(-50%);

      img {
        width: auto;
        max-height: 62px;

        &:last-child {
          transform: rotateY(180deg);
        }
      }
    }

    .xianglu {
      z-index: 10;
      width: auto;
      max-height: 32px;
      position: absolute;
      top: -16%;
      left: 50%;
      transform: translateX(-50%);
    }

    .gongdexiang {
      // width: 50%;
      width: auto;
      max-width: 240px;
      max-height: 120px;
    }
  }
}

.audioBox {
  position: absolute;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
}

@keyframes toUp {
  0% {
    top: -18px;
    opacity: 1;
  }

  100% {
    top: -188px;
    opacity: 0;
  }
}

.text-gd {
  position: absolute;
  top: -18px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  color: #fff;
}

.gdj1 {
  animation: toUp 1s;
}



@keyframes mcRotatez {
  0% {
    transform: rotatez(27deg)
  }

  100% {
    transform: rotatez(7deg)
  }
}

.muchuiImg {
  position: absolute;
  width: 135px;
  left: 51%;
  bottom: 0px;
  transform: rotatez(27deg);
  transform-origin: right;
}

.muchuiImgClick {
  animation: mcRotatez 1s;
}

.dialog-box {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .6);
  position: absolute;
  top: 0;
  z-index: 99999;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  img {
    width: 80%;
    max-width: 380px;
  }

  .closeDialog {
    font-size: 16px;
    color: #ffffff;
    margin-top: 20px;
  }
}
</style>
