import { createRouter, createWebHashHistory } from 'vue-router'
import AppIndex from '../views/appIndex.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    meta:{
      isTabBar:true,
    },
    component: AppIndex
  }, 
  {
    path: '/index', 
    name: 'index',
    component:  () => import ("@/components/index/index.vue")
  },
  {
    path: '/foList', 
    name: 'foList',
    component:  () => import ("@/components/foList/index.vue")
  },
  {
    path: '/details', //多宝阁，商品详情
    name: 'details',
    meta:{
      isTabBar:false,
    },
    component: () => import ("@/components/duobaoge/detail.vue")
  },
  {
    path: '/LFT', //电子木鱼
    name: 'lifotang',
    meta:{
      isTabBar:false,
    },
    component: () => import ("@/components/lifotang/Muyu.vue")
  },
  {
    path: '/moreDeital', //更多详情 ，详细介绍
    name: 'moreDeital',
    meta:{
      isTabBar:false,
    },
    component: () => import ("@/components/moreDeital/moreDeital.vue")
  },
  {
    path: '/xuYuanShu',//许愿树
    name: 'xuYuanShu',
    meta:{
      isTabBar:false,
    },
    component: () => import ("@/components/xuyuanshu/index.vue")
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
