<template>
    <div class="heade-wrap">
        <Marquee style="right:0;"></Marquee>
        <div class="heade-box">
            <div class="flex1">
                <div class="avatar-box">
                    <van-image width="40px" height="40px" fit="cover" round :src="avatar" class="avatar-img" />
                    <div class="userInfo">
                        <h5>{{ userInfo.name }}</h5>
                        <div class="baseType"><img :src="userInfo.gender == 1 ? man : woman" alt=""
                                style="width: 14px;"><span>{{ userInfo.gender == 1 ? '男' : '女' }}</span></div>
                    </div>
                </div>
                <div class="tag-box">
                    <span class="tag-yb"><img src="@/assets/images/100002.png" />{{ ingot }} <span class="add-box"
                            @click="czShow = true">+</span> </span>
                </div> 
                <img :src="IconShareSheet" alt="" class="iconShare" @click="showShare=true">
            </div>
            <chongZhi v-show="czShow" @close="czShow = false"></chongZhi>
            <ShareSheet :show="showShare"  @close="closeShare"></ShareSheet>
        </div>
    </div>

</template>
<script>
import avatar from './images/avatar.svg';
import man from "./images/sexMan.svg";
import woman from "./images/sexWoman.svg";
import chongZhi from "@/components/chongzhi";
import Marquee from "@/components/marquee";
import IconShareSheet from "@/assets/images/shareSheet.svg";
import ShareSheet from "@/components/share-sheet";
export default {
    name: 'HeaDer',
    components: {
        chongZhi,Marquee,ShareSheet
    },
    mixins: [],
    props: {
        MarqueeShow:{
            tyoe:Boolean,
            default:true
        }
    },
    data() {
        return {
            avatar, man, woman,IconShareSheet,
            userInfo: '',
            // ingot: this.$store.state.user.asset.ingot,//999999999
            czShow: false,
            showShare:false,
        }
    },
    computed: {
        ingot() {
            return this.$store.state.user.asset.ingot;//999999999
        }
    },
    watch: {
        '$store.state.user.userInfo': {
            handler(newVal) {
                this.userInfo = newVal;
            },
            deep: true,
            immediate: true
        }
    },
    mounted() {

    },
    methods: {
        closeShare(val){
            console.log('showShare：',val)
            this.showShare=val;
        }
    }
};
</script>
<style lang='less' scoped>
@import "@/assets/css/common.less";
.heade-wrap{
    position: fixed; 
    left: 0;
    width: 100%;
    z-index: 99999;
}
.heade-box {
    padding: 28px 0 10px 10px;

    // background: #fff;
    .avatar-box {
        min-width: 110px;
        max-width: 160px;
        height: 40px;
        border-radius: 40px;
        background: linear-gradient(45deg, #333, transparent);
        margin-right: 10px;
        .flex1();

        .avatar-img {
            flex: 0 0 auto;
        }

        .userInfo {
            margin-left: 8px;
            color: #fff;
            display: flex;
            flex-direction: column;
            // align-items: center;
            justify-content: center;

            .baseType {
                display: flex;
                align-items: center;
                font-size: 12px;
                color: #fff;

                img {
                    margin-right: 2px;
                }
            }

        }

    }

    .tag-box {
        margin-top: 2px;
        display: flex;
        align-items: center;
        font-size: 13px;
        grid-column-gap: 8px;

        .tag-vip {
            background: rgba(225, 225, 225, .2);
            padding: 2px 8px;
            border-radius: 2px;
        }

        .tag-yb {
            min-width: 118px;
            background: rgba(0, 0, 0, .2);
            padding: 5px 8px;
            border-radius: 4px;
            color: rgba(225, 225, 225, .9);
            .flex1();

            img {
                width: 20px;
                // height: 15px;
                margin-right: 10px;
            }

            .add-box {
                margin-left: 12px;
                color: #fff;
                width: 22px;
                height: 22px;
                background: radial-gradient(#fff6, #3f7105 55%);
                border-radius: 4px;
                box-shadow: 0 0 10px #333;
                border: 1px solid #ffffff1f;
                .flex2();
            }
        }
    }

    .iconShare{
        margin-left:auto;
        margin-right: 10px;
        width: 28px;
    }
}
</style>