<template>
    <div class="shareSheetWrap">
        <van-share-sheet v-model:show="showShare" title="立即分享给好友"  :options="options" @select="select" @close="close" class="customShareSheet"/>
    </div>
</template>
<script>
export default {
    name: 'ShareSheet',
    components: {

    },
    mixins: [],
    props: {
        show:{
            type:Boolean,
            default:false
        }
    },
    data() {
        return {
            showShare: false,
            options: [
                [
                    { name: '微信', icon: 'wechat' },
                    { name: '朋友圈', icon: 'wechat-moments' },
                    { name: '微博', icon: 'weibo' },
                    { name: 'QQ', icon: 'qq' },
                ],
                // [
                //     { name: '复制链接', icon: 'link' },
                //     { name: '分享海报', icon: 'poster' },
                //     { name: '二维码', icon: 'qrcode' },
                //     { name: '小程序码', icon: 'weapp-qrcode' },
                // ],
            ],
        }
    },
    computed: {

    },
    watch: {
        show(val){
            this.showShare=val;
        }
    },
    mounted() {

    },
    methods: {
        close(){
            this.$emit('close',false)
        },
        select(option){
            // {name: '朋友圈', icon: 'wechat-moments'}
            console.log(option.name)
        },
    }
};
</script>
<style lang='less' scoped>
/deep/.van-overlay{
    z-index: 888888!important;

}
/deep/.customShareSheet{
    z-index: 999999!important;
}
</style>