<!-- 商店 左下角 -->
<template>
    <div class="sd-wrap"> 
        <div class="shangdian-wrap" @click="ccShow = true">
            <h2 class="title">商店</h2>
            <img src="./images/xiaoheshang.svg" alt="" class="xhsTu">
            <img src="./images/banyuanzhuo.png" alt="" class="banyuanzhuoTu">
            <!-- <canvas ref="zhuozi" width="200px" height="100px" class="canvasBox"></canvas> -->

        </div>
        <div class="chuchuangWrap" v-show="ccShow">
            <div class="cc-close" @click="ccShow = false">
                <img src="./images/close-red.svg" alt="关闭">
            </div>
            <div class="cc-box" v-for="(item) in shangpin" :key="item.name" @click="buyHandle(item)">
                <img :src="item.imgSrc" alt="">
                <p class="name">{{ item.name }}<span style="font-size: 12px;" v-if="item.num">x{{ item.num }}</span></p>
                <!-- <p class="price"><img src="@/assets/images/yuanbao.svg" />{{ item.price }} </p> -->
                <p class="price"><img src="@/assets/images/100002.png" />{{ item.price }} </p>
            </div>
        </div> 
        <TipMessage content="你的元宝不足，是否充值？" :tipShow="tipShow" @confirm="confirmCZ" @close="tipShow=false"></TipMessage>
        <TipMessage :content="buyTipContent" :tipShow="buyTipShow" @confirm="confirmBuy" @close="buyTipShow=false"></TipMessage>
        <chongZhi v-show="czShow" @close="czShow=false"></chongZhi> 
    </div>
</template>
<script>
import xiangzhu from '@/assets/images/ranshaoxiangzhu.png';
import chongZhi from "@/components/chongzhi";
import TipMessage from "@/components/tipMessage";
export default {
    name: 'ShangDian',
    components: {
        chongZhi,TipMessage
    },
    mixins: [],
    props: {

    },
    data() {
        return {
            ccShow: false,//商店橱窗
            tipShow:false,//充值tip
            czShow:false,//充值弹窗
            buyTipContent:"",//购买提示内容
            buyTipShow:false,//购买提示是否显示
            shangpin: [
                {
                    name: '香烛',
                    price: '10',
                    num:2,
                    imgSrc: xiangzhu
                },
                {
                    name: '鲜花',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '佛香',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '涂供养',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '水果',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '食物',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '珠宝',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '衣服',
                    price: '10',
                    imgSrc: xiangzhu
                },
                {
                    name: '茶',
                    price: '10',
                    imgSrc: xiangzhu
                }
            ],
            buyArticle: [],
            buyWp:'',//购买的物品
            
        }
    },
    computed: {
        ingot(){
            return this.$store.state.user.asset.ingot;//资产元宝数
        }
    },
    watch: {

    },
    mounted() {
        // this.canvasZhuozi();
    },
    methods: {
        canvasZhuozi() {
            const box = this.$refs.zhuozi;
            let ctx = box.getContext("2d");
            //  设置canvas的尺寸  

            // 扇形的参数
            var centerX = 100;
            var centerY = 0;
            var radius = 96;
            var startAngle = 0; // 弧度
            var endAngle = Math.PI; // 弧度
            var anticlockwise = false; // 顺时针画扇形

            // 移动到扇形起始点
            ctx.beginPath();
            // 画扇形
            ctx.arc(centerX, centerY, radius, startAngle, endAngle, anticlockwise);

            // 绘制边界
            ctx.lineWidth = 3;
            ctx.strokeStyle = 'blue';
            ctx.stroke();
            ctx.fillStyle = 'blue';
            ctx.fill();
            // 中心白色
            // 移动到扇形起始点
            ctx.beginPath();
            // 画扇形
            ctx.arc(centerX, centerY, 60, startAngle, endAngle, anticlockwise);
            ctx.clip()

            // 绘制边界
            // ctx.lineWidth = 3;
            // ctx.strokeStyle = 'blue';
            // ctx.stroke();
            // ctx.fillStyle = 'white';
            // ctx.fill();
            // // 设置曲面的起点
            // ctx.beginPath();
            // ctx.moveTo(0, 0);

            // // 添加曲面的控制点和终点
            // ctx.quadraticCurveTo(100, 60, 200, 0);

            // // 绘制填充曲面
            // // ctx.fillStyle = 'blue';
            // // ctx.fill();

            // // 绘制轮廓线
            // ctx.strokeStyle = 'black';
            // ctx.stroke();
            // ctx.beginPath();
            // ctx.moveTo(10, 50);
            // ctx.quadraticCurveTo(100, 90, 190, 50);
            // ctx.strokeStyle = 'black';
            // ctx.stroke();
        },
        buyHandle(item) { 
            if(this.ingot<item.price){
                this.tipShow=true;
                return;
            }
            console.log('元宝充足')
            this.buyTipContent=`你确定要购买${item.name}x${item.num}吗`;
            this.buyTipShow=true;
            this.buyWp=item;
   
        },
        // 确定充值
        confirmCZ(){
            this.czShow=true;
            this.tipShow=false;
        },
        // 确定购买
        confirmBuy(){ 
            this.buyTipShow=false;
            const syIngot = this.ingot - this.buyWp.price;
            this.$store.commit('user/setIngot',syIngot);
            console.log('syIngot:',syIngot)
            // this.buyArticle.unshift(this.buyWp);
            // localStorage.setItem('buyArticle', JSON.stringify(this.buyArticle))
        },
    }
};
</script>
<style lang='less' scoped>
.sd-wrap{
    
}
.shangdian-wrap {
    position: fixed;
    z-index: 100;
    width: 200px; 
    bottom: -26px;
    left: 0;
    transform: scale(.5);
    transform-origin: 0;
    .title {
        position: absolute;
        left: 5%;
        top: -50%;
        font-size: 24px;
        color: rgba(0, 138, 119, .8);
    }

    .xhsTu {
        width: 100px;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translateX(-50%) translateY(-130%);
        // display:none;
    }

    .banyuanzhuoTu {
        width: 200px;
    }

    .canvasBox {
        // position:absolute;
        // z-index:9;
    }

}

.chuchuangWrap {
    position: fixed;
    bottom: 115px;
    z-index: 1001;
    /* left: 100px; */
    transform: translateX(27%);
    box-shadow: 5px -5px 10px #4e2c1e;
    // background: rgba(0, 0, 0, 0.5);
    background: linear-gradient(45deg, #321e17, #4e2c1e);
    width: 70%;
    height: 40%;
    max-width: 300px;
    max-height: 300px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);

    .cc-close {
        position: absolute;
        top: -7%;
        right: -20px;
        width: 32px;
        height: 32px;

        img {
            width: 100%;
        }
    }

    .cc-box {
        border-right: 1px solid #b3afaf;
        border-bottom: 1px solid #b3afaf;
        // background: rgba(225, 225, 225, 0.8);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        img {
            width: auto;
            max-height: 30px;
        }

        .name {
            font-size: 18px;
            color: rgba(225, 225, 225, 0.8);
            font-weight: 500;
            margin: 10px 0 2px;
            font-family: cursive;
        }

        .price {
            font-size: 15px;
            // color: rgba(235, 31, 8, 0.8);
            color: rgb(246 218 102);
            font-weight: 900;
            font-family: math;
            display: flex;
            align-items: center;

            img {
                width: 18px;
            }

            span {
                font-size: 10px;
                color: rgba(235, 31, 8, 0.8);
            }
        }
    }
}


</style>