<template>
<div class="tabBarWrap-1">
    <div class="tabBarWrap">
     <ui>
        <li v-for="(item) in tabBarData" :key="item.label" v-show="item.show" :style="{color:activeName==item.label?item.selectedColor:item.color,fontWeight:900}" @click="activeHandel(item)">
            <img :src="activeName==item.label?item.selectedIconPath:item.iconPath" alt="" :style="{width:item.width}">
            {{ item.label }}
        </li>
     </ui>
   </div>
</div>
   
</template>
<script>
import home from './images/home.svg';
import homeSelected from './images/homeSelected.svg';
import gelou from './images/gelou.svg'
import gelouSelected from './images/gelouSelected.svg'
import my from './images/my.svg';
import mySelected from './images/mySelected.svg';
import xuyuanshu from './images/xuyuanshu.svg';
import xuyuanshuSelected from './images/xuyuanshuSelected.svg';
export default {
   name: 'TabBar',
   components: {
     
   },
   mixins: [],
   props: {
     
   },
   data() {
     return {
        activeName:'礼佛堂',
       tabBarData:[
        {
            label:'礼佛堂',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:home,
            selectedIconPath:homeSelected,
            pagePath:'HomeIndex',
            width:'24px',
            show:true,
        },
        {
            label:'坊市',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:gelou,
            selectedIconPath:gelouSelected,
            pagePath:'DuoBaoGe',
            width:'24px',
            show:true,
        },{
            label:'经书',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:gelou,
            selectedIconPath:gelouSelected,
            pagePath:'DuoBaoGe',
            width:'24px',
            show:true,
        },{
            label:'多宝阁',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:gelou,
            selectedIconPath:gelouSelected,
            pagePath:'DuoBaoGe',
            width:'24px',
            show:true,
        },
        {
            label:'我的',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:my,
            selectedIconPath:mySelected,
            pagePath:'WoDe',
            width:'20px',
            show:false,
        },
        {
            label:'许愿树',
            color:'#6f6f6f',
            selectedColor:'#008A77',
            iconPath:xuyuanshu,
            selectedIconPath:xuyuanshuSelected,
            pagePath:'XuyuanShu',
            width:'20px',
            show:false,
        }
       ]
     }
   },
   computed: {
     
   },
   watch: {
     
   },
   mounted() {
     
   },
   methods: {
    activeHandel(item){
        if(this.activeName==item.label) return;
        this.activeName=item.label;
        this.$emit('activeHandel',item.pagePath)
    }
   }
};
</script>
<style scoped>
.tabBarWrap-1{
    background:#fff;
    width:100%;
    height:48px;
}
.tabBarWrap{
    flex:0 0 auto;
    position:fixed;
    bottom:calc(.1px + env(safe-area-inset-bottom));
    left:0;
    z-index:9999;
    background:#fff;
    width:100%;
    height:48px;
    border-top: 1px solid #12121224;
    /* padding-top: calc(28px + env(safe-area-inset-top));
  padding-bottom:env(safe-area-inset-bottom); */
}
ui,li{
    list-style: none;
    height:100%;
    display:flex;
    justify-content: space-evenly;
}
li{
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
    font-size: 14px;
}
</style>