<template>
    <div class="audioWrap">
        <img :src="audioImg" alt="" @click="playAudio" :class="[!play ? 'stop' : '']" ref="play">
        <audio :src="audioSrc" style="display: none;" ref="audio"></audio>
    </div>
</template>
<script>
import playImg from '../../assets/audio/audioPlay.svg';
import stopImg from '../../assets/audio/audioStop.svg';
import audioSrc from "../../assets/audio/六字真言.mp3";
export default {
    name: 'AudioPlay',
    components: {

    },
    mixins: [],
    props: {
    },
    data() {
        return {
            audioSrc,
            play: false,
        }
    },
    watch: {
        '$route': {
            handler() {
                this.play = false;
            },
            immediate: false,
        }
    },
    computed: {
        audioImg() {
            return this.play ? playImg : stopImg
        }
    },
    mounted() {
    },
    methods: {
        playAudio() {
            this.play = !this.play;
            if (this.play) {
                this.$refs.audio.play();
            } else {
                this.$refs.audio.pause();
            }

        },
    }
};
</script>
<style lang='less' scoped>
@keyframes ImgRote {
    0% {
        transform: rotateZ(0deg)
    }

    100% {
        transform: rotateZ(360deg)
    }
}

.audioWrap {
    position: fixed;
    right: 10px;
    top: 68px;
    width: 26px;

    img {
        width: 100%;
        animation: ImgRote 3s linear forwards infinite running;
    }

    .stop {
        animation: ImgRote 3s linear forwards infinite paused;
    }
}
</style>