<!--
  页面类型:
  文件名:baoguoList
  菜单路径:
  功能描述: 包裹列表
  创建时间:2024-04-20 14:54:59
-->
<template>
    <van-action-sheet v-model:show="showList" title="包裹" class="customActionSheet" @close="close">
        <ul class="tabBox">
            <li class="tabItem" v-for="item in navList" :key="item.label"></li>
        </ul>
        <div class="contentBox">
            <div class="contentItem" v-for="(item, index) in contentList" :key="index">
                <p class="content-item-title">{{ item.name }}</p>
            </div>
        </div>
    </van-action-sheet> 
</template>
<script>
export default {
    name: 'BaoguoList',
    components: {

    },
    mixins: [],
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            showList: false,
            navList: [
                {
                    label: '道具',
                }
            ],
            contentList: [
                {
                    name: '鲜花',
                    icon: ''
                }
            ]
        }
    },
    computed: {

    },
    watch: {
        show: {
            handler(val) {
                this.showList = val;
            },
            immediate: true,
        }
    },
    mounted() {

    },
    methods: {
        close() {
            this.$emit('close', false)
        }
    }
};
</script>
<style lang='less' scoped> 
/deep/.van-overlay{
    z-index: 888888!important;

}
/deep/.customActionSheet{
    z-index: 999999!important;
}
