    <Back v-if="backShow"></Back>
    <router-view v-slot="{ Component }">
      <keep-alive>
        <component :is="Component" :class="[backShow ? 'customPaddingTop' : '']" />
      </keep-alive>
    </router-view>
  <!-- </div> -->

</template>

<script>
import Back from '@/components/back-icon';
export default {
  name: 'App',
  components: {
    Back, 
  },
  data() {
    return {
      backShow: false,
    }
  },
  watch: {
    '$route'(val) {
      if (!val.meta.isTabBar) {
        this.backShow = true;
      } else {
        this.backShow = false;
      }
    }
  },
