<!-- 提示信息 -->
<template>
   <div class="cc_tipIcon" :class="[isd?'cc_tipIconAnimation':'']">
        <img src="./tip.svg" alt="">
   </div>
</template>
<script>
export default {
   name: 'TipIcon',
   components: {
     
   },
   mixins: [],
   props: {
     
   },
   data() {
     return {
      isd:true,
      inter:'',
     }
   },
   computed: {
     
   },
   watch: {
     
   },
   mounted() {
      this.time()
   },
   methods: {
     time(){
         this.inter=setInterval(() => {
            this.isd=!this.isd;  
         }, 1000);
         setTimeout(()=>{
            clearInterval(this.inter);
            this.time()
         },3000)
     }
   },
   beforeUnmount() {
      clearInterval(this.inter)
   },
};
</script>
<style lang='less' scoped>
@keyframes fdhd{
   0% { transform: scale(0);}
   25% { transform: scale(1);}
   50% { transform: translateX(-2px); }
   75% { transform: translateX(2px); }
   100%{transform: translateX(0);}
}
.cc_tipIcon{
   width:14px;
   height: 14px;
   // transform: translateX(0);
   
   img{
      width:100%;
      height: 100%;
   }
}
.cc_tipIconAnimation{
   animation: fdhd 1s;
}
</style>